/**
 * Módulo AMD para obtener la hora actual.
 * Define 'block_dynamic_time/time_module'
 */
define([], function() {
    /**
     * Obtiene la hora actual formateada.
     * @returns {string} La hora actual en formato HH:MM:SS.
     */
    function getCurrentTime() {
        const now = new Date();
        const hours = String(now.getHours()).padStart(2, '0');
        const minutes = String(now.getMinutes()).padStart(2, '0');
        const seconds = String(now.getSeconds()).padStart(2, '0');
        return `${hours}:${minutes}:${seconds}`;
    }

    // Exporta la función para que pueda ser utilizada por otros módulos de Moodle AMD.
    return {
        getCurrentTime: getCurrentTime
    };
});